*{box-sizing: border-box}
*::before,*::after{box-sizing: border-box}
.skin{
    position: relative;
}

.san{
    border: 10px solid red ;
    border-color: red transparent  transparent;
    border-bottom: 0;
    width: 0px;
    height: 0px;
    position: relative;
    left: 50%;
    top: 200px;
    margin-left: -5px;
}
.yuan{
    position: absolute;
    width: 20px;
    height: 6px;
    top:-16px;
    left: -10px;
    border-radius: 10px 10px 0 0  ;
    background-color: red;
}
